export const BASE_URL = 'https://api.mestogram.frontend.nomoredomains.rocks';

const checkResponse = (res) => (res.ok ? res.json() : Promise.reject(`Ошибка ${res.statusText}`));

export const register = (password, email) => fetch(`${BASE_URL}/signup`, {
  method: 'POST',
  credentials: 'include',
  headers: {
    'Content-Type': 'application/json',
  },
  body: JSON.stringify({ password, email }),
})
  .then(checkResponse);

export const authorize = (password, email) => fetch(`${BASE_URL}/signin`, {
  method: 'POST',
  credentials: 'include',
  headers: {
    'Content-Type': 'application/json',
  },
  body: JSON.stringify({ password, email }),
})
  .then(checkResponse);

export const getContent = () => fetch(`${BASE_URL}/users/me`, {
  method: 'GET',
  credentials: 'include',
})
  .then(checkResponse);

export const signOut = () => fetch(`${BASE_URL}/signout`, {
  method: 'GET',
  credentials: 'include',
})
  .then(checkResponse);
