class Api {
  constructor(config) {
    this._url = config.url;
  }  

  _handleResponse = (res) => {
    if (res.ok) {
      return res.json();
    }
    return Promise.reject(`Ошибка: ${res.status}`);
  }

  getCards() {
    return fetch(`${this._url}/cards`, {
      method: 'GET',
      credentials: 'include'
    })
    .then(this._handleResponse)
  }

  getUserInformation() {
    return fetch(`${this._url}/users/me`, {
      method: 'GET',
      credentials: 'include'
    })
    .then(this._handleResponse)
  }

  setUserAvatar(data) {
    return fetch(`${this._url}/users/me/avatar`, {
      method: 'PATCH', 
      headers: {
        'Content-Type': 'application/json'
      },
      credentials: 'include',
      body: JSON.stringify({
        avatar: data.avatar
      }) 
    })
    .then(this._handleResponse)    
  }
 
  setUserInfo(data) {
    return fetch(`${this._url}/users/me`, {
      method: 'PATCH',
      credentials: 'include',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({          
        name: data.name,
        about: data.about,  
      })      
    })
    .then(this._handleResponse)     
  }

  setLikeOnCard(id) {
    return fetch(`${this._url}/cards/${id}/likes`, {
      method: 'PUT',
      credentials: 'include'
    })
    .then(this._handleResponse)    
  }

  removeLikeOnCard(id) {
    return fetch(`${this._url}/cards/${id}/likes`, {
      method: 'DELETE', 
      credentials: 'include'
    })
    .then(this._handleResponse)    
  }  

  addCards(text, link) {
    return fetch(`${this._url}/cards`, {
      method: 'POST',
      credentials: 'include',
      headers: { 
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        name: text,
        link: link})  
    })
    .then(this._handleResponse)    
  }

  deleteCard(id) {
    return fetch(`${this._url}/cards/${id}`, {
      method: 'DELETE',
      credentials: 'include',
      headers: {
        'Content-Type': 'application/json'
      }
    })
    .then(this._handleResponse)    
  }
}

const api = new Api({
  url: 'https://api.mestogram.frontend.nomoredomains.rocks'
})

export default api;